body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f4f6;
  font-family: Inter, sans-serif;
}

.hidden {
  display: none;
}

.no-hover-highlight {
  -webkit-tap-highlight-color: transparent;
}

.chip:focus-visible, .tab-button:focus-visible, button:focus-visible, [tabindex="0"]:focus-visible, .dashboard-grid-card:focus-visible, .star-rating input:focus-visible + label, .sidebar-tab-button:focus-visible {
  outline-offset: 2px;
  border-radius: 4px;
  outline: 2px solid #3b82f6;
}

.chip, .tab-button, .modal, #view-sidebar, .star-rating label, .sidebar-tab-button, button {
  transition: all .15s ease-in-out;
}

.chip {
  align-items: center;
  padding: .25rem .75rem;
  display: inline-flex;
}

.chip-rank-indicator {
  color: #fff;
  background-color: #1e3a8a;
  border-radius: 9999px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  min-width: 1.75rem;
  height: 1.25rem;
  padding: 0 .4rem;
  font-size: .75rem;
  font-weight: 600;
  line-height: 1;
  display: inline-flex;
}

.chip .chip-rank-indicator {
  margin-right: .5rem;
}

.chip-rank-indicator .emoji {
  margin-right: .15rem;
  font-size: .65rem;
  line-height: 1;
}

.chip.disabled {
  opacity: .5;
  cursor: not-allowed;
  pointer-events: none;
  color: #6b7280;
  background-color: #f3f4f6;
  border-color: #d1d5db;
}

.tab-button.active {
  color: #2563eb;
  background-color: #eff6ff;
  border-color: #3b82f6;
  font-weight: 600;
}

.tab-button.active .tab-count {
  color: #3b82f6;
}

.modal-overlay {
  z-index: 50;
  opacity: 0;
  visibility: hidden;
  backdrop-filter: blur(2px);
  background-color: #0009;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

.modal-overlay.visible {
  opacity: 1;
  visibility: visible;
}

.modal {
  background-color: #fff;
  border-radius: .5rem;
  width: 90%;
  max-width: 400px;
  padding: 1.5rem;
  transform: scale(.95);
  box-shadow: 0 10px 15px -3px #0000001a, 0 4px 6px -2px #0000000d;
}

.modal-overlay.visible .modal {
  transform: scale(1);
}

.dashboard-grid-card {
  text-align: center;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #e5e7eb;
  border-radius: .5rem;
  flex-direction: column;
  justify-content: center;
  min-height: 95px;
  padding: .75rem;
  display: flex;
  position: relative;
  box-shadow: 0 1px 2px #0000000d;
}

.dashboard-grid-card .card-title {
  color: #1f2937;
  white-space: nowrap;
  text-overflow: ellipsis;
  pointer-events: none;
  margin-bottom: .25rem;
  font-size: .875rem;
  font-weight: 600;
  display: block;
  overflow: hidden;
}

.dashboard-grid-card .card-title .emoji {
  margin-right: .25rem;
  font-size: .75rem;
}

.dashboard-grid-card .card-count {
  color: #6b7280;
  pointer-events: none;
  margin-bottom: .25rem;
  font-size: .75rem;
  display: block;
}

.dashboard-grid-card.selected .card-count {
  color: #1d4ed8;
  font-weight: 500;
}

.dashboard-grid-card.all-selected .card-count {
  color: #15803d;
  font-weight: 600;
}

.dashboard-rank-indicator-wrapper {
  pointer-events: none;
  position: absolute;
  top: .3rem;
  right: .3rem;
}

.dashboard-rank-indicator-wrapper .chip-rank-indicator {
  border: 1px solid #ffffff80;
  margin-right: 0;
  box-shadow: 0 1px 2px #00000026;
}

.dashboard-stars {
  color: #fbbf24;
  letter-spacing: 1px;
  pointer-events: none;
  height: 1.2em;
  font-size: .8rem;
  line-height: 1;
}

#view-sidebar {
  z-index: 60;
  visibility: hidden;
  background-color: #f9fafb;
  flex-direction: column;
  width: 85%;
  max-width: 350px;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  transform: translateX(100%);
  box-shadow: -2px 0 10px #0000001a;
}

#view-sidebar.visible {
  visibility: visible;
  transform: translateX(0);
}

.sidebar-header {
  background-color: #fff;
  border-bottom: 1px solid #e5e7eb;
  justify-content: space-between;
  align-items: center;
  gap: .5rem;
  padding: 1rem 1.5rem;
  display: flex;
}

.sidebar-title-container {
  flex-grow: 1;
  align-items: center;
  gap: .5rem;
  min-width: 0;
  display: flex;
}

.sidebar-title {
  color: #1f2937;
  white-space: nowrap;
  text-overflow: ellipsis;
  flex-shrink: 1;
  font-size: 1.125rem;
  font-weight: 600;
  overflow: hidden;
}

.sidebar-title .emoji {
  flex-shrink: 0;
  font-size: 1rem;
}

.sidebar-title .chip-rank-indicator {
  flex-shrink: 0;
  margin-left: .5rem;
}

.sidebar-close-btn {
  color: #6b7280;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 9999px;
  flex-shrink: 0;
  padding: .5rem;
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1;
}

.sidebar-content {
  flex-direction: column;
  flex-grow: 1;
  padding: 0;
  display: flex;
  overflow-y: auto;
}

.sidebar-chip {
  color: #374151;
  background-color: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 9999px;
  align-items: center;
  margin-bottom: .5rem;
  padding: .5rem .75rem;
  font-size: .875rem;
  display: flex;
}

.sidebar-chip.selected {
  color: #1e40af;
  background-color: #eff6ff;
  border-color: #93c5fd;
  font-weight: 500;
}

.sidebar-chip .chip-rank-indicator {
  margin-right: .75rem;
}

.sidebar-tab-nav {
  background-color: #fff;
  border-bottom: 1px solid #e5e7eb;
  padding: 0 1.5rem;
  display: flex;
}

.sidebar-tab-button {
  text-align: center;
  color: #6b7280;
  cursor: pointer;
  border-bottom: 2px solid #0000;
  flex-grow: 1;
  padding: .75rem .5rem;
  font-size: .875rem;
  font-weight: 500;
}

.sidebar-tab-button.active {
  color: #3b82f6;
  border-color: #3b82f6;
  font-weight: 600;
}

.sidebar-tab-panel {
  flex-grow: 1;
  padding: 1.5rem;
  overflow-y: auto;
}

.review-display-area {
  margin-bottom: 1rem;
}

.review-display-area .stars {
  color: #fbbf24;
  letter-spacing: 1px;
  margin-bottom: .25rem;
  font-size: 1.1rem;
}

.review-display-area .stars.unrated {
  color: #d1d5db;
}

.review-display-area .comment {
  color: #4b5563;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-size: .875rem;
  font-style: italic;
}

.review-display-area .no-review {
  color: #9ca3af;
  font-size: .875rem;
  font-style: italic;
}

#review-editor-form h4 {
  color: #374151;
  margin-bottom: .75rem;
  font-weight: 600;
}

.star-rating {
  flex-direction: row-reverse;
  justify-content: center;
  margin-bottom: .5rem;
  display: flex;
}

.star-rating input[type="radio"] {
  display: none;
}

.star-rating label {
  color: #d1d5db;
  cursor: pointer;
  padding: 0 .1em;
  font-size: 2.5rem;
}

.star-rating input[type="radio"]:checked ~ label, .star-rating:not(:checked) > label:hover, .star-rating:not(:checked) > label:hover ~ label {
  color: #fbbf24;
}

.rating-description {
  text-align: center;
  color: #4b5563;
  min-height: 1.25em;
  margin-bottom: 1rem;
  font-size: .875rem;
  font-style: italic;
}

.review-comment textarea {
  resize: vertical;
  border: 1px solid #d1d5db;
  border-radius: .375rem;
  width: 100%;
  min-height: 60px;
  padding: .5rem .75rem;
  font-size: .875rem;
}

.comment-char-count {
  text-align: right;
  color: #9ca3af;
  margin-top: .25rem;
  font-size: .75rem;
}
